<template>
  <v-app>
    <v-main class="img">
      <v-app-bar
        color="#6A76AB"
        dark
        shrink-on-scroll
        prominent
        :src="randomImg"
        fade-img-on-scroll
      >
        <h2 style="font-size:45px;">Welcome to {{ new Date().getFullYear() }} TEST</h2>
      </v-app-bar>
      <v-app-bar>
        <v-tabs
          align-with-title
          color="#FFFFFF"
          background-color="#708090"
          next-icon="mdi-arrow-right-bold-box-outline"
          prev-icon="mdi-arrow-left-bold-box-outline"
          show-arrows
          dark
        >
          <v-tab @click="goto('/')">
            <v-icon>mdi-home</v-icon>
            {{this.$t('Home')}}
          </v-tab>
          <v-tab @click="goto('/programList')">{{this.$t('Activities')}}</v-tab>
          <v-tab @click="goto('/menu')">{{this.$t('Menu')}}</v-tab>
          <v-tab @click="goto('/maps')">{{this.$t('Map')}}</v-tab>
          <v-spacer></v-spacer>
          <v-col sm="1">
            {{localeLanguage()}}
            <v-select v-model="selectName" :items="Language" label="翻譯"></v-select>
          </v-col>
        </v-tabs>
      </v-app-bar>
      <!--v-btn @click="bt">msg</v-btn-->
      <Snackbar style="height:0"></Snackbar>
      <router-view />
    </v-main>
    <v-card height="125px">
      <v-footer>
        <v-card flat tile width="100%" class="black lighten-1 text-center">
          <v-card-text>
            <v-btn v-for="icon in icons" :key="icon" class="mx-4" icon>
              <v-icon size="24px" color="white" @click="goto(icon)">{{ icon }}</v-icon>
            </v-btn>
          </v-card-text>
          <v-card-text class="white--text">
            {{ new Date().getFullYear() }}.{{ new Date().getMonth()+1 }}.{{ new Date().getDate() }} —
            <strong>TEST</strong>
          </v-card-text>
        </v-card>
      </v-footer>
    </v-card>
  </v-app>
</template>

<script >
import Snackbar from "@/components/_partial/Snackbar";
import { mapActions } from "vuex";
export default {
  name: "App",
  components: {
    Snackbar,
  },
  data: () => ({
    icons: ["mdi-home", "mdi-phone", "mdi-email", "mdi-calendar"],
    selectName: "", //预设中文
    Language: ["中文", "English"],
    randomImg:'https://picsum.photos/1920/1080?random'
  }),
  methods: {
    localeLanguage() {
      //转换语言
      if (this.selectName === "中文") {
        this.$i18n.locale = "zh";
      } else if (this.selectName === "English") {
        this.$i18n.locale = "en";
      }
    },
    goto(name) {
      if (name != this.$route.path) {
        this.$router.push(name);
      }
      if (name == "mdi-home") {
        this.$router.push("/");
        this.$router.go(0);
      }
      if (name == "mdi-calendar") {
        this.$router.push("/calendar");
        this.$router.go(0);
      }
    },
    bt() {
      //this.$store.dispatch('increment');
      /*this.$store.dispatch("snackbar/openSnackbar", {
          msg: "信息測試"
        });
      */
      this.test({ msg: "尚無內容" });
    },
    ...mapActions({
      test: "snackbar/openSnackbar",
      count: "snackbar/increment",
    }),
  },
};
</script>
<style>
@import "./css/config.css";
.img {
  background: url("./assets/img/background02.jpg");
  background-repeat: repeat-y;
  background-size: contain;
}
</style>