<template>
  <div class="show">
    <a href="https://www.google.com/maps/dir/?api=1&origin=&destination=台北車站&travelmode=walking">
      <v-btn color="#1DE9B6">導航</v-btn>
    </a>
    <v-btn @click="currentPosition" color="#5DADE2">本人位置</v-btn>
    <v-btn @click="foodCurrentPosition" color="#FF7433">餐廳位置</v-btn>
    <!--div id="map"></div-->
    <!-- 使用vue2-google-maps生成-->
    <GmapMap ref="mapRef" id="map" :center="map.center" :zoom="map.zoom" map-type-id="terrain">
      <!-- InfoWindow -->
      <GmapInfoWindow
        :key="'info'+index"
        v-for="(f, index) in infoWindows"
        :options="f.infoOptions"
        :position="f.infoPosition"
        :opened="f.infoOpened"
        :content="f.infoContent"
        @closeclick="f.infoOpened=false"
      />
      <!-- Marker -->
      <GmapMarker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :clickable="true"
        :draggable="false"
        :animation="m.Animat"
        :label="m.label"
        :title="m.title"
        :icon="m.icon"
        @click="markerAnimat(google,index)"
      />
    </GmapMap>
  </div>
</template>
<script>
import { gmapApi } from "vue2-google-maps";
export default {
  data() {
    return {
      map: {
        zoom: 15,
        center: { lat: 25.0476, lng: 121.517529 },
      },
      markers: [
        {
          position: { lat: 0, lng: 0 }, //本人位置
          label: "", //vue2-google-maps里面是没有办法控制label的位置的
          Animat: null,
          title: "A點",
        },
        {
          position: { lat: 25.0476, lng: 121.517529 }, //目的標誌
          label: "",
          Animat: null,
          title: "B點",
          icon: ""//"http://maps.google.com/mapfiles/kml/shapes/snack_bar.png",
        },
      ],
      infoWindows: [
        {
          infoPosition: { lat: 0, lng: 0 },
          infoContent: null,
          infoOpened: false,
          infoCurrentKey: null,
          infoOptions: {
            pixelOffset: {
              width: 0,
              height: -35,
            },
            content: "<p><H3>資訊:本人位置</H3></p>",
          },
        },
        {
          infoPosition: { lat: 25.0476, lng: 121.517529 },
          infoContent: null,
          infoOpened: false,
          infoCurrentKey: null,
          infoOptions: {
            pixelOffset: {
              width: 0,
              height: -35,
            },
            content:
              "<p><H3>資訊:台北車站</H3></p><p>地址:台北市中正區北平西路3號</p><p>電話:0987654321</p><p>電子信箱:test123@test.com</p>",
          },
        },
      ],
      watchID: "",
      options: {
        enableHighAccuracy: true,
        maximumAge: 30000,
        timeout: 27000,
      },
    };
  },
  methods: {
    markerAnimat(google, index) {
      //動畫
      if (this.markers[index].Animat !== null) {
        this.markers[index].Animat = null;
      } else {
        this.markers[index].Animat = google.maps.Animation.BOUNCE;
      }
      this.infoWindow(google, index);
    },
    infoWindow(google, index) {
      //信息視窗
      if (this.infoWindows[index].infoOpened == true)
        this.infoWindows[index].infoOpened = false;
      else this.infoWindows[index].infoOpened = true;
    },
    error(positionError) {
      var code = positionError.code;
      if (code == 1) {
        alert("取得地理資訊失敗，因為此頁面沒有獲取地理位置信息的權限。");
      } else if (code == 2) {
        alert("取得地理資訊失敗，因為至少有一個地理位置信息內的資訊回傳了錯誤。");
      } else if (code == 3) {
        alert("取得地理資訊超過時限，利用PositionOptions.timeout 來定義取得地理資訊的時限。");
      } else {
        alert("未知錯誤:"+code);
      }
    },
    success(position) {
      var lat = this.markers[1].position.lat;
      var lng = this.markers[0].position.lng;
      console.log(lat + "," + lng);
      console.log(position.coords.latitude + "," + position.coords.longitude);
      this.setCurrentPosition(
        position.coords.latitude,
        position.coords.longitude
      );
    },
    currentPosition() {
      navigator.geolocation.clearWatch(this.watchID); //呼叫前把之前刪除
      if (navigator.geolocation) {
        /* geolocation is available */
        this.watchID = navigator.geolocation.watchPosition(
          this.success,
          this.error,
          this.options
        );
      } else {
        /* geolocation IS NOT available */
      }
    },
    setCurrentPosition(latitude, longitude) {
      /* 更改目前本人位置 */
      this.markers[0].position.lat = latitude;
      this.markers[0].position.lng = longitude;
      this.infoWindows[0].infoPosition.lat = latitude;
      this.infoWindows[0].infoPosition.lng = longitude;
      /* 畫面移至本人位置 */
      this.$refs.mapRef.$mapPromise.then((map) => {
        map.panTo({ lat: latitude, lng: longitude });
      });
    },
    foodCurrentPosition() {
      /* 畫面移至餐廳位置 */
      this.$refs.mapRef.$mapPromise.then((map) => {
        map.panTo({ lat: this.map.center.lat, lng: this.map.center.lng });
      });
      navigator.geolocation.clearWatch(this.watchID); //刪除追蹤本人位置
    },
  },
  mounted() {
    /*ref="mapRef"
    this.$refs.mapRef.$mapPromise.then((map) => {
      map.panTo({ lat: 1.38, lng: 103.8 });
    });*/
  },
  computed: {
    google: gmapApi,
  },
};
</script>
<style>
#map {
  height: 400px;
  width: 100%;
}
</style>
