<template>
  <div>
    <v-carousel
      cycle
      height="550"
      hide-delimiter-background
      show-arrows-on-hover
      delimiter-icon
      class="formSize"
    >
      <v-carousel-item v-for="(n,index) in ex" :key="index">
        <img v-bind:src="n.foodImg" />
        <v-spacer style="color:#000;">
          {{n.name}}
          <v-btn :class="favs[index].fav!=0 ? 'red--text' : ''" icon @click="likeFav(index)">
            <v-icon :dense="true">mdi-heart</v-icon>
          </v-btn>
        </v-spacer>
        <v-rating v-model="ratings[index]"></v-rating>
        <!--span v-if="n.count!=''">喜愛數:{{n.count}}</span>
        <span v-else>{{info(index)}}</span-->
      </v-carousel-item>
    </v-carousel>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      ex: [
        {
          name: "自訂文字或圖片",
          count: "",
          foodImg: require("../assets/img/food5.png"),
        },
        {
          name: "自訂文字或圖片",
          count: "",
          foodImg: require("../assets/img/food2.png"),
        },
        {
          name: "自訂文字或圖片",
          count: "",
          foodImg: require("../assets/img/food6.png"),
        },
        {
          name: "自訂文字或圖片",
          count: "",
          foodImg: require("../assets/img/food3.png"),
        },
        {
          name: "自訂文字或圖片",
          count: "",
          foodImg: require("../assets/img/food4.png"),
        },
        {
          name: "自訂文字或圖片",
          count: "",
          foodImg: require("../assets/img/food1.png"),
        },
      ],
      favs: this.$store.state.menuinfo.favs,
      ratings: this.$store.state.menuinfo.ratings,
    };
  },
  methods: {
    ratingNum(){
      alert(this.ratings)
    },
    likeFav(index) {
      //計算喜歡~因不是存取數據庫刷新歸0,無法紀錄
      this.$store.commit("menuinfo/set_fav", index);
      
      if (this.favs[index].fav == 1) {
        this.updata(index, "+");
      } else {
        this.updata(index, "-");
      }
    },
    updata(index, sign) {
      axios
        .post(
          "http://localhost:8081/likeMenu/menu",
          this.$qs.stringify({ name: this.ex[index].name, sign: sign })
        )
        .then((response) => {
          this.ex[index].count = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    info(index) {
      axios
        .post(
          "http://localhost:8081/likeMenu/menu",
          this.$qs.stringify({ name: this.ex[index].name, sign: "*" })
        )
        .then((response) => {
          this.ex[index].count = response.data;
          //this.ex=response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style scoped>
.formSize {
  text-align: center;
  padding: 50px 0px 0px 0px;
}
</style>