const menuinfo ={
    namespaced:true,
    state:{
        favs:[{fav:0},{fav:0},{fav:0},{fav:0},{fav:0},{fav:0}],
        ratings:[0,0,0,0,0,0]
    },        
    mutations:{
        set_fav(state,index){
            if(state.favs[index].fav==0)
                state.favs[index].fav=1;
            else
                state.favs[index].fav=0;
        }
    }
}
export default menuinfo;