import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import router from './plugins/router'
import store from './plugins/store'
import i18n from './plugins/i18n'
import qs from 'qs'

Vue.prototype.$qs = qs
Vue.config.productionTip = false


new Vue({
  vuetify,
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
