const snackbar = {
    namespaced:true,
    state: {
        msg: '', // snackbar的訊息
        visible: false, // 是否顯示snackbar
        showClose: true, // 是否顯示關閉按鈕
        timeout: 6000, // 顯示後的自動關閉時間
        color: 'cyan darken-2', // 關閉按鈕的顏色
        count:0
    },
    mutations:{
        OPEN_SNACKBAR(state,options){
            state.visible = true;
            state.msg = options.msg;
        },
        CLOSE_SNACKBAR(state){
            state.visible = false;
        },
        setShowClose(state,isShow){
            state.showClose = isShow;
        },
        setTimeout(state,timeout){ 
            state.timeout = timeout;
        },
        setColor(state,color){
            state.color = color;
        },
        increment(state) {
            state.count++
        }
    },
    actions:{
        openSnackbar(context , options){
            let timeout = context.state.timeout;
            context.commit('OPEN_SNACKBAR',{
                msg: options.msg
            });
            
            setTimeout(() => {
                context.commit('CLOSE_SNACKBAR')
            }, timeout);
        },
        increment(context) {
            context.commit('increment')
          }
    }
}

export default snackbar;