<template>
  <div>
    <div class="row" style="width:30%">
    <v-subheader></v-subheader>
    <v-btn
        icon
        class="ma-2"
        @click="$refs.calendar.prev()">
        <v-icon>mdi-chevron-left</v-icon>
    </v-btn>
    <v-select
          v-model="type"
          :items="types.key"
          dense
          outlined
          hide-details
          class="ma-2"
          label="type"
          @change="changeType"
    ></v-select>
    <v-btn
          icon
          class="ma-2"
          @click="$refs.calendar.next()">
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
    <v-subheader></v-subheader>
    </div>
    <v-subheader></v-subheader>
    <v-calendar
        ref="calendar"
          v-model="value"
          :weekdays="weekday"
          :type="type"
          :events="events"
          :event-overlap-threshold="30"
          :event-color="getEventColor"
          @change="getEvents">
    </v-calendar>
    <div class="container">
            敘述:<input v-model="textA" style="border-style:solid;">
            <v-text-field v-text="'時間: '+value"/>
        <div class="row"> 
            <v-col cols="1" sm="1">開始時間:</v-col>
            <v-col cols="3" sm="2">
                <v-select  class="text-md-center" cols="1" sm="1" v-model="dateA" :items="dates"></v-select>
            </v-col>
            <v-col cols="3" sm="2">
                <v-select class="text-md-center" cols="1" sm="1" v-model="dateA_1" :items="dates2"></v-select>
            </v-col>
            <v-col cols="3" sm="2">
                <v-select class="text-md-center" cols="1" sm="1" v-model="color" :items="colors.key"></v-select>
            </v-col>
        </div>
        <div class="row"> 
            <v-col cols="1" sm="1">結束時間:</v-col>
            <v-col cols="3" sm="2">
                <v-select  class="text-md-center" cols="1" sm="1" v-model="dateB" :items="dates"></v-select>
            </v-col>
            <v-col cols="3" sm="2">
                <v-select class="text-md-center" cols="1" sm="1" v-model="dateB_1" :items="dates2"></v-select>
            </v-col>
        </div>
      <v-btn @click="btn">输入</v-btn><v-btn @click="rebtn">移除</v-btn>
    </div>
  </div>
</template>
<script>
export default {
    name:'calendar',
    data() {
        return {
            textA:'',
            dateA:'',
            dateA_1:'',
            dateB:'',
            dateB_1:'',
            dates:['1點','2點','3點','4點','5點','6點','7點','8點','9點','10點','11點','12點','13點','14點','15點','16點','17點','18點','19點','20點','21點','22點','23點','00點'],
            dates2:['0分','5分','10分','15分','20分','25分','30分','35分','40分','45分','50分','55分'],
            value:'',
            weekday:[0,1,2,3,4,5,6],
            type:'month',
            types: {key:['月','週','日','四天'],value:['month', 'week', 'day', '4day']},
            events: [],
            color:'',
            colors: {key:['藍色','紅色','靛青','深紫色','青色','綠色','橘色','灰色'],value:["blue","red", "indigo", "deep-purple", "cyan", "green", "orange", "grey darken-1"]},
            name:'活動',
            mintime:null,
        }
    },
    methods:{
        getEvents({start/*,end*/}) {
            const events = [];
            const min = new Date(`${start.date}T00:00:00`)
            this.mintime = min.getTime()-86400000;
            //const max = new Date(`${end.date}T23:59:59`)
            //const firstTimestamp = (min.getTime()-86400000)+(86400000*28)+66600000//+天+时 毫秒1000
            //const first = new Date(firstTimestamp - (firstTimestamp % 900000));
            //const second =  new Date(first.getTime());

            for(let i=0;i<this.events.length;i++){
                events.push({
                    name: this.events[i].name,
                    start: this.events[i].start,
                    end: this.events[i].end,
                    color: this.events[i].color,
                    timed: true,
                })
            }
            this.events = events
        },
        rebtn(){
            for(let i=0;i<this.events.length;i++){
                var d=new Date(this.events[i].start);
                var str=d.getFullYear()+'-'+(d.getMonth()+1)+'-'+d.getDate();
                if(this.value == str){
                    this.events.splice(i,1);
                }
            }
            
        },
        btn(){
            var key=''
            for(let i=0;i<this.colors.key.length;i++){
                key = this.colors.key[i];
                if(this.color == key){
                    this.color = this.colors.value[i]
                    break;
                }
            }
            if(this.dateA == '' ||this.dateA_1 == '' || this.dateB == '' ||this.dateB_1 == '' || this.textA == '' || this.value == ''){
                if(this.dateA == '' || this.dateB == ''||this.dateA_1 == '' || this.dateB_1 == '')
                    alert('时间未选择');
                if(this.textA == '')
                    alert('内容未填写');
                if(this.value == '')
                    alert('日期未选择');
                return false;
            }
            var date=this.value.split('-');
            var dateH=this.dateA.replace('點',"");
            var datem=this.dateA_1.replace('分',"");

            var dateHB=this.dateB.replace('點',"");
            var datemB=this.dateB_1.replace('分',"");
            if(Number(dateH)>Number(dateHB)||Number(datem)>Number(datemB)&&Number(dateH)==Number(dateHB)){
                alert('開始時間比結束早了...');
                return false;
            }
            var firstTimestamp1 = this.mintime+(86400000*date[2])+(3600000 * dateH)+(60*datem*1000)//+天+时 毫秒1000
            var first1 = new Date(firstTimestamp1);
            var secondTimestamp1 = (this.mintime+(86400000*date[2])+(3600000 * dateHB)+(60*datemB*1000))-firstTimestamp1//+天+时 毫秒1000
            var second1 =  new Date(first1.getTime()+ secondTimestamp1);
            var events1 = [];
            for(let i=0;i<this.events.length;i++){
                events1.push({
                    name: this.events[i].name,
                    start: this.events[i].start,
                    end: this.events[i].end,
                    color: this.events[i].color,
                    timed: true,
                })
            }
            events1.push({
                    name: this.textA,
                    start: first1,
                    end: second1,
                    color: this.color,
                    timed: true,
            })
            this.color = key;
            this.events =events1;
        },
        getEventColor (event) {
            return event.color
        },
        changeType(){
            var name='';
            for(let i=0;i<this.types.key.length;i++){
                name = this.types.key[i];
                if(this.type == name){
                    this.type = this.types.value[i];
                    break;
                }
            }
        }
    }
}
</script>
<style>
.v-select__selection {
    width: 100%;
    justify-content: center;
}
</style>