import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en from './en.json'
import zh from './zh.json'
Vue.use(VueI18n)

const DEFAULT_LANG = 'zh'

const messages = {
  zh:zh,
  en:en
}

const i18n = new VueI18n({
  locale: DEFAULT_LANG,
  messages: messages,
})

export default i18n